<template>
  <div>
    <el-dialog
      title="(monolith)(multi-repo)"
      top="7vh"
      width="60%"
      :visible.sync="isShowMutiDialog"
    >
      <pre>{{ mutiRepoText }}</pre>
    </el-dialog>

    <el-dialog
      title="(mono-repo)()"
      width="66%"
      :visible.sync="isShowMonoDialog"
    >
      <pre>{{ monoRepoText }}</pre>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "ShowRepoDir",
  computed: {
    mutiRepoFormattedText() {
      return this.mutiRepoText.replace(/\n/g, "<br>");
    },
  },
  data() {
    return {
      isShowMutiDialog: false,
      isShowMonoDialog: false,
      mutiRepoText: ` .
 api            # protobuf*pb.go
 assets         # (logo)
 cmd            # 
 configs        # 
 deployments    # dockerk8s
 docs           # 
 internal       # 
     cache        # 
     config       # Go
     dao          # 
     ecode        # 
     handler      # http
     model        # 
     routers      # http
     rpcclient    # grpc
     server       # httpgrpc
     service      # grpc
     types        # http
 pkg            # 
 scripts        # 
 test           # 
 third_party    # protobuf
 Makefile       # 
 go.mod         # go 
 go.sum         # go 
      `,
      monoRepoText: ` .
 api
     server1       # 1protobuf*pb.go
     server2       # 2protobuf*pb.go
     server3       # 3protobuf*pb.go
     ...
 server1        # 1(multi-repo)
 server2        # 2(multi-repo)
 server3        # 3(multi-repo)
 ...
 third_party    # protobuf
 go.mod         # go 
 go.sum         # go 

`,
    };
  },
};
</script>

<style scoped>
pre {
  white-space: pre-wrap;
  word-wrap: break-word;
  text-align: left;
  margin-left: 8%;
  margin-right: 8%;
  padding-left: 30px;
  border: 1px solid #ccc;
}
</style>



