<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3 style="color: #666;">{{ title }}</h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="" prop="dbDriver">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.dbDriver"
              placeholder=""
              filterable
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChangeDB"
            >
              <el-option
                v-for="(item, index) in dbDriverNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                
              </div>
            </el-tooltip>
          </el-form-item>

          <el-form-item label="dsn" prop="dsnAddr">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.dsnAddr"
              placeholder="user:password@(127.0.0.1:3306)/db_name"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                <b>mysql</b><b>mongodb</b><b>postgresql</b>dsn<br />
                dsn<b>user:password@(host:port)/db_name</b><br />
                userpassword <b>@</b> <b>:</b> <b>(</b>
                <b>)</b> <br /><br />
                postgresqldsnschemadsn<br />
                <b>?search_path=schema1&search_path=schema2</b><br /><br />
                <b>sqlite</b>sqlite db<br />
                <b>/tmp/test.db</b>  <b>C:\Users\Desktop\test.db</b>
              </div>
            </el-tooltip>
            <el-link
              style="margin-left: 10px; font-size: 16px;"
              type="primary"
              @click="listTables(true)"
              ></el-link
            >
          </el-form-item>
          <el-form-item label="" prop="tableName">
            <el-select
              style="width: 80%; font-size: medium"
              v-model="ruleForm.tableName"
              placeholder=""
              clearable
              filterable
              multiple
              @blur="selectBlur"
              @clear="selectClear"
              @change="selectChange"
            >
              <el-checkbox
                v-model="isSelectedAllTable"
                @change="selectAllTables"
                style="padding: 10px 0px 15px 20px; font-weight: bold; color: #409EFF;"
                ></el-checkbox
              >
              <el-badge
                v-model="ruleForm.tableName.length"
                type="primary"
                style="padding: 10px 18px 12px 0px; position: absolute; right: 0;"
              ></el-badge>
              <el-option
                v-for="(item, index) in tableNames"
                :key="index"
                :label="item.label"
                :value="item.value"
              />
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="serverName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                <b>user</b><b>user</b><br />
                "-""_"<br />
                your-service  your_service
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="module" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                <b>user</b><br />
                <b>user</b>go.modmodule
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="Model" v-if="isShowEmbedModel">
            <el-switch v-model="ruleForm.embed"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                gorm.Modelidcreated_at<br />
                updated_atdeleted_at  4 <br />
                Model<br />
                Model
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="CRUD api ">
            <el-switch v-model="ruleForm.isExtendedApi"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                5CRUD api<br />
                5CRUD api4api<br />
                 DeleteByIDs, GetByCondition, ListByIDs, ListByLastID
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="ruleForm.suitedMonoRepo"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                <b>(monolith)</b><br />
                <b>(multi-repo)</b><br /><br />
                <b>(mono-repo)</b
                ><br />
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData"></el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            ></el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
export default {
  name: "MicroService",
  data() {
    return {
      title: "service CRUD",
      path: "micro-service",
      helpInfo: `
      <p>sqlservicegrpc apiCRUD</p>
      <br />
      <p>1. <b>api</b><b>internal</b><b>sqlgrpc</b><b>protobufgrpc</b></p>
      <p>2. :  <b style="color: green">make proto</b></p>
      <p>3. :  <b style="color: green">make run</b></p>
      <p>4.  <b>Goland</b>  <b>VS Code</b>  <span style="color: chocolate">internal/service/xxx_client_test.go</span> CRUD api</p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isShowEmbedModel: true,
      isDownloading: false,

      ruleForm: {
        dsnAddr: "",
        tableName: [],
        dbDriver: "",
        moduleName: "",
        serverName: "",
        embed: false,
        isExtendedApi: false,
        suitedMonoRepo: false,
      },
      rules: {
        dsnAddr: [
          { required: true, message: "dsn", trigger: "blur" },
        ],
        tableName: [
          { required: true, message: "", trigger: "change" },
        ],
        dbDriver: [
          { required: true, message: "", trigger: "blur" },
        ],
        moduleName: [
          { required: true, message: "go module", trigger: "blur" },
        ],
        serverName: [
          { required: true, message: "", trigger: "blur" },
        ],
      },

      tableNames: [],
      isSelectedAllTable: false,
      dbDriverNames: [
        { label: "mysql", value: "mysql" },
        { label: "mongodb", value: "mongodb" },
        { label: "postgresql", value: "postgresql" },
        { label: "sqlite", value: "sqlite" },
      ],
    };
  },
  created: function() {
    this.adaptiveWindows();
    this.listDbDrivers();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.dsnAddr = data.data.dsn;
          this.ruleForm.dbDriver = data.data.dbDriver;
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          // this.ruleForm.embed = data.data.embed;

          this.checkDBDriver(this.ruleForm.dbDriver);
          this.listTables(false);
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          const apiUrl = appConfig.spongeServiceAddr + "/generate";
          var selectTableNames = this.ruleForm.tableName.join(",");
          const arg = `micro service --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --db-driver=${this.ruleForm.dbDriver} --db-dsn=${this.ruleForm.dsnAddr} --db-table=${selectTableNames} --embed=${this.ruleForm.embed} --suited-mono-repo=${this.ruleForm.suitedMonoRepo} --extended-api=${this.ruleForm.isExtendedApi}`;
          this.$refs.downloadCode.execution(
            apiUrl,
            this.path,
            arg,
            this.helpInfo,
          );
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.tableNames = [];
      this.ruleForm = {
        tableName: [],
        embed: false,
        suitedMonoRepo: false,
      };
    },
    listTables(isTip) {
      if (this.ruleForm.dsnAddr == "") {
        return;
      }
      const apiUrl = appConfig.spongeServiceAddr + "/listTables";
      const formdata = {
        dsn: this.ruleForm.dsnAddr,
        dbDriver: this.ruleForm.dbDriver,
      };
      axios
        .post(apiUrl, formdata, { timeout: 3000 })
        .then(res => {
          const { data } = res;
          if (isTip) {
            data.code == 0
              ? this.$message.success("")
              : this.$message.error("" + data.msg);
          }

          this.tableNames = data.data;
        })
        .catch(err => {
          if (isTip) {
            this.$message.error(
              "" +
                this.ruleForm.dbDriver +
                " dsn" +
                this.ruleForm.dbDriver +
                "",
            );
          }
          // console.log(err);
        });
    },
    listDbDrivers() {
      const apiUrl = appConfig.spongeServiceAddr + "/listDrivers";
      axios
        .get(apiUrl)
        .then(res => {
          const { data } = res;
          if (data.code == 0) {
            this.dbDriverNames = data.data;
          }
        })
        .catch(err => {
          // console.log(err);
        });
    },
    selectBlur(e) {
      if (e.target.value !== "") {
        this.value = e.target.value + "()";
        this.$forceUpdate(); // 
      }
    },
    selectClear() {
      this.value = "";
      this.$forceUpdate();
    },
    selectChange(val) {
      if (val.length === this.tableNames.length) {
        this.isSelectedAllTable = true;
      } else {
        this.isSelectedAllTable = false;
      }
      this.value = val;
      this.$forceUpdate();
    },
    selectAllTables(val) {
      this.ruleForm.tableName = [];
      if (val) {
        this.tableNames.forEach(item => {
          this.ruleForm.tableName.push(item.value);
        });
      }
    },

    selectChangeDB(val) {
      this.value = val;
      this.$forceUpdate();
      this.checkDBDriver(val);
    },

    checkDBDriver(val) {
      if (val == "mongodb") {
        this.isShowEmbedModel = false;
      } else {
        this.isShowEmbedModel = true;
      }
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.el-select /deep/ .el-input__inner {
  font-size: medium;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.tooltip-content b {
  color: #bb6f00;
}
</style>



