<template>
  <el-row :gutter="10">
    <el-col :span="spanSize" :offset="offsetSize">
      <div class="title">
        <h3>
          <a
            href="https://go-sponge.com/zh-cn/web-development-protobuf"
            target="_blank"
            style="color: #e403a4"
            >protobufweb</a
          >
          <span style="color: #777; font-weight:normal;">
            web</span
          >
        </h3>
      </div>
      <div class="form-style">
        <el-form
          :label-position="labelPosition"
          label-width="25%"
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
        >
          <el-form-item label="proto" prop="protobufFile">
            <el-link
              style="margin-left: 10%;padding-bottom: 5px; font-size: 16px;"
              type="primary"
              @click="showProtoFileDemo"
              >protobuf</el-link
            >
            <el-upload
              class="upload-demo"
              drag
              multiple
              accept=".proto"
              action="#"
              :auto-upload="false"
              :on-change="handleOnchange"
              :on-remove="handleOnRemove"
              :file-list="fileList"
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                <em>proto</em>
              </div>
              <!-- <div class="el-upload__tip" slot="tip">
                proto
              </div> -->
            </el-upload>
          </el-form-item>

          <el-form-item
            label=""
            prop="serverName"
            style="margin-top: 50px;"
          >
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.serverName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                "-""_"<br />
                your-service  your_service
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="module" prop="moduleName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.moduleName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                go.modmodule
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="" prop="projectName">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.projectName"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                <br />
                yourProject  your-project
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="docker">
            <el-input
              style="width: 80%; font-size: medium"
              v-model.trim="ruleForm.repoAddr"
            ></el-input>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                docker<b>image-repo-host</b><br />
                dockerk8sdocker<br />
                docker<br />
                
              </div>
            </el-tooltip>
          </el-form-item>
          <el-form-item label="">
            <el-switch v-model="ruleForm.suitedMonoRepo"></el-switch>
            <el-tooltip
              class="item"
              effect="light"
              placement="top"
              style="margin-left: 10px;"
            >
              <i class="el-icon-question"></i>
              <div slot="content" class="tooltip-content">
                <b>(monolith)</b><b
                  ><br />
                  (multi-repo)</b
                ><br />
                git<br /><el-button
                  type="text"
                  @click="showMutiRepoDir"
                  ></el-button
                ><br /><br />
                <b>(mono-repo)</b><br />
                git<br />
                <br /><br />
                <el-button type="text" @click="showMonoRepoDir"
                  ></el-button
                >
              </div>
            </el-tooltip>
          </el-form-item>
        </el-form>
        <div style="margin: 30px auto;"></div>

        <div class="conform-btns">
          <el-button size="default" @click="clearFormData"></el-button>
          <el-button
            type="primary"
            size="default"
            @click="submitForm('ruleForm')"
            :disabled="isDownloading"
            :loading="isDownloading"
            ></el-button
          >
          <DownloadCode
            ref="downloadCode"
            @update-is-downloading="updateIsDownloading"
          ></DownloadCode>
          <ShowRepoDir ref="showRepoDir"></ShowRepoDir>
        </div>
      </div>

      <el-dialog
        top="5vh"
        title="user.proto"
        :visible.sync="showProtoDemoDialogVisible"
        width="66%"
        center
      >
        <el-input
          type="textarea"
          :rows="20"
          autosize
          readonly
          v-model="protoDemoContent"
        >
        </el-input>
      </el-dialog>
    </el-col>
  </el-row>
</template>

<script>
import axios from "axios";
import DownloadCode from "@/components/DownloadCode";
import ShowRepoDir from "@/components/ShowRepoDir";
export default {
  name: "WebHTTPPb",
  data() {
    return {
      title: "",
      path: "web-http-pb",
      helpInfo: `
      <p><b>protobufweb</b> web <a href="https://go-sponge.com/zh-cn/web-development-protobuf" target="_blank">web(protobuf)</a></p>
      <br />
      <p>1. </p>
      <p>2. :  <b style="color: green">make proto</b></p>
      <p>3.  <span style="color: chocolate">internal/handler/xxx.go</span>,  <span style="color: chocolate">panic("implement me")</span></p>
      <p>4. :  <b style="color: green">make run</b></p>
      <p>5.  <a href="http://localhost:8080/apis/swagger/index.html" target="_blank">http://localhost:8080/apis/swagger/index.html</a>api</p>
      <br />
      <p><b>GoGinProtobufZapPrometheusJaegerOpenTracing</b></p>
      `,

      labelPosition: "right",
      windowWidth: document.documentElement.clientWidth,
      spanSize: 16,
      offsetSize: 4,
      isDownloading: false,

      fileList: [],
      ruleForm: {
        moduleName: "",
        serverName: "",
        projectName: "",
        repoAddr: "",
        protobufFile: "",
        suitedMonoRepo: false,
      },
      rules: {
        moduleName: [
          { required: true, message: "go module", trigger: "blur" },
        ],
        serverName: [
          { required: true, message: "", trigger: "blur" },
        ],
        projectName: [
          { required: true, message: "", trigger: "blur" },
        ],
        protobufFile: [
          { required: true, message: "proto", trigger: "change" },
        ],
      },

      showProtoDemoDialogVisible: false,
      protoDemoContent: `syntax = "proto3";

package api.user.v1;

import "google/api/annotations.proto";
import "protoc-gen-openapiv2/options/annotations.proto";

// usergo moduleuserv1
option go_package = "user/api/user/v1;v1";

option (grpc.gateway.protoc_gen_openapiv2.options.openapiv2_swagger) = {
  host: "localhost:8080"
  base_path: ""
  info: {
    title: "user api docs";
    version: "2.0";
  };
  schemes: HTTP;
  schemes: HTTPS;
  consumes: "application/json";
  produces: "application/json";
};

service user {
  // Login 
  rpc Login(LoginRequest) returns (LoginReply) {
    option (google.api.http) = {
      post: "/api/v1/login"
      body: "*"
    };
    option (grpc.gateway.protoc_gen_openapiv2.options.openapiv2_operation) = {
      summary: "",
      description: "",
    };
  }

  // rpchttp
  //    1. rpcgoogle.api.http SayHi
  //    2. rpcstream ClientSteamServerSteamBidiSteam

  //rpc SayHi(HelloRequest) returns (HelloReply) {}
  //rpc ClientSteam(stream HelloRequest) returns (HelloReply) {}
  //rpc ServerSteam(HelloRequest) returns (stream HelloReply) {}
  //rpc BidiSteam(stream HelloRequest) returns (stream HelloReply) {}
}

message LoginRequest {
  string email = 1;
  string password = 2;
}

message LoginReply {
  string token = 1;
}

message HelloRequest {
  string name = 1;
}

message HelloReply {
  string msg = 1;
}
`,
    };
  },
  created: function() {
    this.adaptiveWindows();

    const apiUrl = appConfig.spongeServiceAddr + "/record/" + this.path;
    axios
      .get(apiUrl)
      .then(res => {
        const { data } = res;
        if (data.code == 0) {
          this.ruleForm.moduleName = data.data.moduleName;
          this.ruleForm.serverName = data.data.serverName;
          this.ruleForm.projectName = data.data.projectName;
          this.ruleForm.repoAddr = data.data.repoAddr;
          this.ruleForm.protobufFile = "";
        }
      })
      .catch(err => {
        // console.log(err);
      });
  },

  components: { DownloadCode, ShowRepoDir },

  mounted() {
    window.onresize = () => {
      return (() => {
        this.$nextTick(() => {
          this.adaptiveWindows();
        });
      })();
    };
  },

  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 
          let fd = new FormData();
          this.fileList.forEach(item => {
            fd.append("files", item.raw);
          });
          const uploadURL = appConfig.spongeServiceAddr + "/uploadFiles";
          axios
            .post(uploadURL, fd)
            .then(res => {
              if (res.data.code == 0) {
                // 
                const apiUrl = appConfig.spongeServiceAddr + "/generate";
                const arg = `web http-pb --module-name=${this.ruleForm.moduleName} --server-name=${this.ruleForm.serverName} --project-name=${this.ruleForm.projectName} --repo-addr=${this.ruleForm.repoAddr} --protobuf-file=${res.data.data} --suited-mono-repo=${this.ruleForm.suitedMonoRepo}`;
                this.$refs.downloadCode.execution(
                  apiUrl,
                  this.path,
                  arg,
                  this.helpInfo,
                );
              } else {
                this.$message.error(res.data.msg);
              }
            })
            .catch(err => {
              this.$message.error("");
            });
        } else {
          return false;
        }
      });
    },
    updateIsDownloading(value) {
      this.isDownloading = value;
    },
    clearFormData() {
      this.fileList = [];
      this.ruleForm = {};
    },
    handleOnchange(file, fileList) {
      this.ruleForm.protobufFile = file.name;
      this.fileList = fileList;

      if (fileList.length !== 0) {
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },
    handleOnRemove(file, fileList) {
      this.fileList = fileList;
      if (fileList.length === 0) {
        this.ruleForm.protobufFile = "";
        this.$refs.ruleForm.validateField("protobufFile");
      }
    },

    showProtoFileDemo() {
      this.showProtoDemoDialogVisible = true;
    },

    showMutiRepoDir() {
      this.$refs.showRepoDir.isShowMutiDialog = true;
    },
    showMonoRepoDir() {
      this.$refs.showRepoDir.isShowMonoDialog = true;
    },

    adaptiveWindows() {
      this.windowWidth = document.documentElement.clientWidth;
      if (this.windowWidth < 900) {
        this.spanSize = 24;
        this.offsetSize = 0;
      } else if (this.windowWidth <= 1200) {
        this.spanSize = 18;
        this.offsetSize = 3;
      } else {
        this.spanSize = 16;
        this.offsetSize = 4;
      }
    },
  },
};
</script>

<style scoped>
.conform-btns {
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
}

.el-button {
  font-family: "Microsoft YaHei UI";
  font-weight: normal;
  font-size: 16px;
  /* width: 120px; */
  text-align: center;
}

.title {
  font-family: "Microsoft YaHei UI";
  margin: 0 !important;
  text-align: center;
  vertical-align: middle;
  margin-bottom: 25px !important;
}

.form-style {
  border: 2px solid #ddd;
  box-shadow: 0px 0px 10px #ccc;
  border-radius: 10px;
  padding: 40px 40px;
}

.form-style /deep/ .el-form-item__label {
  font-family: "Microsoft YaHei UI";
  font-size: 16px;
  color: #111;
}

.upload-demo /deep/ .el-upload-dragger {
  height: 60%;
  width: 150%;
}

.el-upload-dragger .el-icon-upload {
  margin-top: 20px;
}
.el-upload__text {
  line-height: 1.5;
  margin-bottom: 20px;
}

.showProtoDemoDiv {
  width: 85%;
  margin: auto;
  font-size: 16px;
  color: black;
  background: rgba(255, 255, 255, 0.5);
  line-height: 20px;
  border: 1px solid #ccc;
  padding: 5px 20px;
  margin-bottom: 20px;
}

.el-icon-question {
  font-size: 20px;
  vertical-align: middle;
  color: #dcdfe6;
}

.tooltip-content {
  padding: 5px;
  color: #c46600;
  font-size: 14px;
  line-height: 150%;
}

.tooltip-content b {
  color: #bb6f00;
}

.title a {
  text-decoration: none;
}
.title a:hover {
  text-decoration: underline;
}

.el-dialog__wrapper /deep/ .el-dialog .el-dialog__body {
  padding: 10px 50px 30px 50px;
}

.el-textarea /deep/ .el-textarea__inner {
  background: rgba(220, 220, 200, 0.3);
  border: 1px solid #ddd;
}
</style>



